import styled from "styled-components";

export const StyledHeader = styled.header`
  padding: 45px;
  user-select: none;
  text-align: center;
  @media screen and (max-width: 768px) {
    padding: 15px 30px;
  }
`;

export const StyledHr = styled.hr`
  width: 100%;
  border: 1px solid #eaeaea;
`;
